.friend-item{
    font-size: 1.4em;
    margin-bottom: 0.5em;
    margin-top: 0.5em;
    margin-left: auto;
    margin-right: auto;
    max-width: 10em;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    text-align: center;
}

.friend-btn{
    border: 2px solid;
    margin-left: 1em;
    background-color: var(--col-gray);
    float: right;
    height: 1.5em;
    width: 4em;
    border-radius: 1em;
    margin: auto;
}

.friend-dialog{ 
    background-color: var(--col-gray);
    border: none;
    
}

.friend-container{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
}

.block{
    color: var(--col-red);
}

.group{
    color: var(--col-green);
}

.no-show{
    display: none;
}