.Login-page{
    display: grid;
    grid-template-rows: 50vh 50vh;
}

.login-box{
    padding-top: 1em;
    padding-bottom: 1em;
    border: 1px solid red;
    width: 15em;
    background-color: var(--col-light-gray);
    display: grid;
    border-radius: 1em;
    margin: auto;
}

.login-small{
    background-color: var(--col-green);
    border: none;
    margin: auto;
    border-radius: 2em;
    height: 2.5em;
    width: 8.33em;
    font-size: 1em;
}

.login-small:focus{
    background-color: var(--col-green-darker);
}

.login-input{
    background-color: var(--col-green);
    border: none;
    width: 12.5em;
    height: 2.5em;
    margin: auto;
    border-radius: 1.667em;
    text-align: center;
    font-size: 1em;
    -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
    -moz-box-sizing: border-box;    /* Firefox, other Gecko */
    box-sizing: border-box;  
    padding-left: 1em;
    padding-right: 1em;
}

.login-box1{
    grid-template-rows: 1fr 1fr 1fr;
    height: 15em;
}

.login-box2 {
    margin-top: 0.833em;
    grid-template-rows: 1fr 1fr 1fr 1fr;
    height: 18em;
}

::placeholder{
    color: var(--col-gray);
    opacity: 0.75;
}