.modal {
    background-color: var(--col-gray);
    margin: auto;
    color: var(--col-white);
    border-radius: 2em;
    top: -10em;
    min-height: 20em;
    min-width: 15em;
    padding: 0.5em;
}

.top-modal{
    display: grid;
    grid-template-columns: 1fr 3em;
}

#modal-input{ 
    border-radius: 2em;
    width: calc(14em / 1.189 );
    height: 2.52em;
    border: 2px solid var(--col-green);
    background-color: var(--col-gray);
    -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
    -moz-box-sizing: border-box;    /* Firefox, other Gecko */
    box-sizing: border-box;  
    padding-left: .420em;
    padding-right: .420em;
    margin: auto;
    font-size: 1em;
    color: var(--col-white);
}

#close-modal {
    background-color: var(--col-gray);
    border: 2px solid var(--col-red);
    border-radius: 4em;
    height: 3em;
    width: 3em;
    margin: auto;
}

.friend-query-result {
    padding-left: 0.5em;
}