.modal {
    background-color: var(--col-gray);
    margin: auto;
    color: var(--col-white);
    border-radius: 2em;
    top: -10em;
    min-height: 20em;
    min-width: 15em;
    padding: 0.5em;
}

#modal-group {
    background-color: var(--col-light-gray);
    min-height: 18em;
    min-width: 13em;
    max-width: 13em;
    max-height: 18em;
    color: var(--col-gray);
    overflow: scroll;
}

#close-modal-group {
    background-color: var(--col-red);
    border: 2px solid var(--col-red);
    border-radius: 4em;
    height: 3em;
    width: 3em;
    margin: auto;
}

.group-wrapper{
    display: grid;
    grid-template-rows: 4em 1fr;
}

.top-wrapper{
    display: grid;
    grid-template-columns: 3em 1fr;
    margin-bottom: 1em;
}

#new-group-button {
    border-radius: 4em;
    margin: auto;
    width: 8.7em;
    height: calc(2.6em - 4px);
    border: 4px solid var(--col-green);
    font-size: 1.15em;
    color: var(--col-gray);
}

.group-item{
    color: var(--col-gray);
    font-size: 1.15em;
    margin: auto;
    padding-top: 0.25em;
}

#create-group-modal{
    margin: auto;
    top: -10em;
    border-radius: 2em;
    padding: 0.5em;
    display: grid;
    grid-template-columns: 3em 1fr 3em;
}

#create-group-modal:not([open]){
    display:none;
  }

#create-group-final{
    background-color: var(--col-white);
    border: 3px solid var(--col-green);
    border-radius: 4em;
    height: 3em;
    width: 3em;
    margin: auto;
}

#group-search-input{
    font-size: 1em;
    text-align: center;
    width: 10em;
    border: none;
}

.float-l {
    font-size: 1.4em;
    margin-bottom: 0.5em;
    margin-top: 0.5em;
    margin-left: auto;
    margin-right: auto;
    max-width: 10em;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    text-align: left;
}