.Home-page {
    height: 100vh;
    display: grid;
    grid-template-rows: 6em 1fr 5em;
    overflow: hidden;
}

.bottom {
    max-width: 100vw;
    display: grid;
    grid-template-columns: 1fr calc(4em + 6px);
}

#main-input{
    border-radius: 4em;
    background-color: var(--col-gray);
    border: 5px solid var(--col-green);
    margin: 0.5357em; /* 0.75 / 1.4 want em is niet meer 1 want fontsize */
    color: var(--col-white);
    text-align: center;
    font-size: 1.4em;
    -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
    -moz-box-sizing: border-box;    /* Firefox, other Gecko */
    box-sizing: border-box;  
}

#send-msg {
    background-color: var(--col-green);
    border: none;
    border-radius: 4em;
    margin: 0.75em;
    margin-left: 0;
}

.top {
    display: grid;
    grid-template-columns: 5em 1fr 5em;
    margin-bottom: 1em;
    max-width: 100vw;
}

.current-chat{
    font-size: 2.5em;
}

.sym-button {
    height: 4em;
    width: 4em;
    color: var(--col-white);
    background-color: var(--col-gray);
    border: 2px solid var(--col-red);
    border-radius: 4em;
    margin: auto;
    z-index: 10;
    transition: 300ms;
}

.current-chat {
    margin: auto;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    max-width: calc(6em);
}

.main{
    clear: both;
    overflow: scroll;
    max-width: 100vw;
}

.fopen{
    margin-left: 1em;
    width: 20em;
    height: fit-content;
    pointer-events: all;
}

.friend-search{
    pointer-events: all;
    height: 2em;
    width: 15em;
    border-radius: 1em;
}

.overlay{
    position: fixed;
    background-color: var(--col-gray);
    mix-blend-mode: multiply;
    z-index: 2;
    transition: 300ms;
}

.active {
    height: 100vh;
    width: 100vh;
}  

.hidden {
    display: none;
    transition: ease-in 300ms;
    width: 0px;
}

.wrapper {
    height: 20em;
    width: 15em;
    display: grid;
    grid-template-rows: 3em 1fr;
}

.goofball {
    margin: auto;
}

.no-click{
    pointer-events: none;
}