.Message{
    background-color: var(--col-light-gray);
    font-size: 1.2em;
    border-radius: 0.833em;
    padding: 0.5em;
    max-width: 14em;
    width: fit-content;
    overflow-wrap: break-word; 
    word-wrap: break-word;
    height: auto;
    margin: 0.5em;
    margin-top: 0.2em;
    margin-bottom: 0.2em;
    clear: both;
    float: left;
}

.self{
    float: right;
}
